import { useEffect, useState } from 'react';
import { supabase } from '../lib/supabase';
import { PredictionChart } from '../components/PredictionChart';
import { PredictionsTable } from '../components/PredictionsTable';
import { TrainingProgress } from '../components/TrainingProgress';

export function AgentDashboard() {
  const [status, setStatus] = useState<{
    trainingCount: number;
    lastTrainingTime: string | null;
    accuracy: number;
    isTraining: boolean;
  } | null>(null);
  
  const [predictions, setPredictions] = useState<{
    date: Date;
    price: number;
  }[]>([]);

  const [historicalData, setHistoricalData] = useState<{
    date: Date;
    price: number;
  }[]>([]);

  const [chartViewMode, setChartViewMode] = useState<'historical' | 'prediction'>('historical');

  const [trainingProgress, setTrainingProgress] = useState<{
    currentEpoch: number;
    totalEpochs: number;
    loss: number;
  } | null>(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Pobierz status agenta
        const { data: agentState } = await supabase
          .from('agent_state')
          .select('*')
          .eq('agent_id', 'bitcoin_predictor')
          .single();

        if (agentState) {
          setStatus({
            trainingCount: agentState.training_count || 0,
            lastTrainingTime: agentState.last_training_time,
            accuracy: agentState.accuracy || 0,
            isTraining: false
          });
        } else {
          setStatus({
            trainingCount: 0,
            lastTrainingTime: null,
            accuracy: 0,
            isTraining: false
          });
        }

        // Pobierz najnowsze predykcje
        const { data: latestTraining } = await supabase
          .from('training_results')
          .select('*')
          .order('timestamp', { ascending: false })
          .limit(1)
          .single();

        if (latestTraining?.predictions) {
          setPredictions(latestTraining.predictions.map((p: any) => ({
            date: new Date(p.date),
            price: p.price
          })));
        }

        // Pobierz dane historyczne
        const { data: historical } = await supabase
          .from('historical_data')
          .select('*')
          .order('timestamp', { ascending: true });

        if (historical) {
          setHistoricalData(historical.map(h => ({
            date: new Date(h.timestamp),
            price: h.price
          })));
        }
      } catch (error) {
        console.error('Bd pobierania danych:', error);
        // Ustaw domylny status w przypadku bdu
        setStatus({
          trainingCount: 0,
          lastTrainingTime: null,
          accuracy: 0,
          isTraining: false
        });
      }
    };

    fetchData();
    const interval = setInterval(fetchData, 5 * 60 * 1000); // Odwieaj co 5 minut

    return () => clearInterval(interval);
  }, []);

  if (!status) {
    return (
      <div className="min-h-screen bg-gray-100 flex items-center justify-center">
        <div className="text-xl font-semibold text-gray-700">
          adowanie danych...
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-100 p-4">
      <div className="max-w-7xl mx-auto space-y-6">
        <div className="bg-white rounded-lg shadow-lg p-6">
          <h1 className="text-3xl font-bold mb-4">Bitcoin AI Predictor</h1>
          
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
            <div className="bg-blue-50 p-4 rounded-lg">
              <h3 className="text-lg font-semibold text-blue-800">Liczba treningw</h3>
              <div className="text-2xl font-bold text-blue-600">
                {status.trainingCount}
              </div>
            </div>
            
            <div className="bg-green-50 p-4 rounded-lg">
              <h3 className="text-lg font-semibold text-green-800">Dokadno modelu</h3>
              <div className="text-2xl font-bold text-green-600">
                {status.accuracy.toFixed(2)}%
              </div>
            </div>
            
            <div className="bg-purple-50 p-4 rounded-lg">
              <h3 className="text-lg font-semibold text-purple-800">Ostatni trening</h3>
              <div className="text-lg font-bold text-purple-600">
                {status.lastTrainingTime 
                  ? new Date(status.lastTrainingTime).toLocaleString('pl-PL')
                  : 'Brak'}
              </div>
            </div>
            
            <div className="bg-orange-50 p-4 rounded-lg">
              <h3 className="text-lg font-semibold text-orange-800">Status</h3>
              <div className="text-lg font-bold text-orange-600">
                {status.isTraining ? 'Trening w toku' : 'Gotowy'}
              </div>
            </div>
          </div>

          {trainingProgress && (
            <div className="mb-6">
              <TrainingProgress
                isTraining={status.isTraining}
                currentEpoch={trainingProgress.currentEpoch}
                totalEpochs={trainingProgress.totalEpochs}
                loss={trainingProgress.loss}
              />
            </div>
          )}
        </div>

        <div className="bg-white rounded-lg shadow-lg p-6">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-bold">Wykres Bitcoin</h2>
            <div className="flex gap-2">
              <button
                onClick={() => setChartViewMode('historical')}
                className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                  chartViewMode === 'historical'
                    ? 'bg-blue-600 text-white'
                    : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                }`}
              >
                Widok historyczny
              </button>
              <button
                onClick={() => setChartViewMode('prediction')}
                className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                  chartViewMode === 'prediction'
                    ? 'bg-blue-600 text-white'
                    : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                }`}
              >
                Tylko predykcja
              </button>
            </div>
          </div>
          <PredictionChart
            historicalData={historicalData}
            predictions={predictions}
            viewMode={chartViewMode}
          />
        </div>

        <div className="bg-white rounded-lg shadow-lg p-6">
          <h2 className="text-xl font-bold mb-4">Szczegowe predykcje</h2>
          <PredictionsTable predictions={predictions} />
        </div>
      </div>
    </div>
  );
}